import { createSlice } from "@reduxjs/toolkit"

let initialState = {
    aboutInfo: {
        adress: 'Moscow, Russia',
        age: 66,
        firstName: 'Kill',
        lastName: 'Real',
    }
}
const hookFormSlice = createSlice({
    name: 'hookFormSlice',
    initialState,
    reducers: {
        setFirstName: (state, action) => {
            state.aboutInfo.firstName = action.payload
        },
        setSubmitData: (state, action) => {
            state.aboutInfo = action.payload
        }
    }
})

export const setHookFormData = (data) => (dispatch) => {
    dispatch(setSubmitData(data))
}

export const { setFirstName, setSubmitData } = hookFormSlice.actions
export default hookFormSlice.reducer