import React, { useEffect, useState } from "react";
import { withAuthRedirect } from "../../Hoc/withAuthRedirect";
import { connect, useDispatch, useSelector } from "react-redux";
import { compose } from "redux";
import { setFirstName, setSubmitData } from "../../Redux/hookFormReducer";
import { useForm, Controller } from "react-hook-form";
import { Button, Input } from "antd";
import S from './hookForm.module.css'

const HookForm = () => {
    const { control, handleSubmit, reset, formState: { errors } } = useForm({ mode: "onChange" })
    const [editMode, setEditMode] = useState(false)
    const [editValue, setEditValue] = useState('')
    const aboutInfo = useSelector((store) => store.hookForm.aboutInfo)
    console.log('ПЕРЕРИСОВКА')
    const dispatch = useDispatch()
    const onSubmit = (data) => {
        dispatch(setSubmitData(data))
        reset()
    }
    const onFirstNameSubmit = () => {
        setEditMode(false)
        let firstName = editValue
        dispatch(setFirstName(firstName))
        setEditValue('')
    }
    const onInputChange = (e) => {
        setEditValue(e.currentTarget.value)
    }
    return (
        <div >
            <form onSubmit={handleSubmit(onSubmit)} >
                <div className={S.inputBlock}>
                    {errors.firstName?.type === "required" && (
                        <p role="alert">Введите имя</p>
                    )}
                    {errors.firstName?.type === "pattern" && (
                        <p role="alert">Без цифр пожалуйста</p>
                    )}
                    {errors.firstName?.type === "maxLength" && (
                        <p role="alert">Не больше 20-и символов</p>
                    )}
                    <div className={S.elem}>
                        <label className={S.label} >Имя</label>
                        <Controller
                            name="firstName"
                            rules={{ required: true, pattern: /^[A-Za-zА-Яа-я]+$/i, maxLength: 20 }}
                            control={control}
                            aria-invalid={errors.firstName ? "true" : "false"}
                            defaultValue={''}
                            render={({ field }) => <Input placeholder="Введите данные" className={S.input} {...field} />
                            }
                        />
                    </div>
                    {errors.lastName?.type === "required" && (
                        <p role="alert">Введите фамилию</p>
                    )}
                    {errors.lastName?.type === "pattern" && (
                        <p role="alert">Без цифр пожалуйста</p>
                    )}
                    {errors.lastName?.type === "maxLength" && (
                        <p role="alert">Не больше 20-и символов</p>
                    )}
                    <div className={S.elem}>
                        <label className={S.label}  >Фамилия</label>
                        <Controller
                            name="lastName"
                            rules={{ required: true, pattern: /^[A-Za-zА-Яа-я]+$/i, maxLength: 20 }}
                            aria-invalid={errors.lastName ? "true" : "false"}
                            control={control}
                            defaultValue=""
                            render={({ field }) => <Input placeholder="Введите данные" className={S.input} {...field} />}
                        />

                    </div>
                    {errors.adress?.type === "required" && (
                        <p role="alert">Введите адрес</p>
                    )}
                    <div className={S.elem}>
                        <label className={S.label}  >Адресс</label>
                        <Controller
                            name="adress"
                            rules={{ required: true }}
                            aria-invalid={errors.adress ? "true" : "false"}
                            control={control}
                            defaultValue=""
                            render={({ field }) => <Input placeholder="Введите данные" className={S.input} {...field} />}
                        />

                    </div>
                    {errors.age?.type === "required" && (
                        <p role="alert">Введите возраст</p>
                    )}
                    {errors.age?.type === "maxLength" && (
                        <p role="alert">Неужели тебе 100 лет?</p>
                    )}
                    {errors.age?.type === "pattern" && (
                        <p role="alert">Тут возраст только в цифрах</p>
                    )}
                    <div className={S.elem}>
                        <label className={S.label} >Возраст</label>
                        <Controller
                            name="age"
                            rules={{ required: true, maxLength: 2, pattern: /[0-9]/i }}
                            aria-invalid={errors.age ? "true" : "false"}
                            control={control}
                            defaultValue=""
                            render={({ field }) => <Input placeholder="Введите данные" className={S.input} {...field} />}
                        />

                    </div>
                </div>
                <div className={S.submitInput}><Input type='submit' defaultValue={'Подтвердить'} style={{ width: '120px' }} /></div>
                {/* <Button htmlType="submit" className={S.submitBtn}>Submit</Button> */}
            </form>
            <div>Вывод данных из Store</div>
            <div onClick={() => setEditMode(true)}>First name - {!editMode && <label >{aboutInfo.firstName}</label>}</div>  {editMode && <div> <input defaultValue={aboutInfo.firstName} onChange={onInputChange} /> <button onClick={onFirstNameSubmit}>Save</button></div>}
            <div>Last name - <label >{aboutInfo.lastName}</label></div>
            <div>Age - <label>{aboutInfo.adress}</label></div>
            <div>Adress name - <label>{aboutInfo.age}</label></div>
        </div>
    )
}
export default compose(
    withAuthRedirect
)(HookForm);